<template>
  <div class="location">
    <div class="box">
      <el-form>
        <el-form-item>
          <label>其它页面广告图</label>
        </el-form-item>
      </el-form>
      <template>
        <el-table :data="entityList" @selection-change="handleSelectionChange" style="width: 100%">
          <el-table-column label="页面位置">
            <template slot-scope="scope">
              <p>{{scope.row.position}}</p>
            </template>
          </el-table-column>
          <el-table-column label="标题">
            <template slot-scope="scope">
              <p>{{scope.row.title}}</p>
            </template>
          </el-table-column>
          <el-table-column label="关键字">
            <template slot-scope="scope">
              <p>{{scope.row.keyword}}</p>
            </template>
          </el-table-column>
          <el-table-column label="关键字">
            <template slot-scope="scope">
              <p>{{scope.row.intro}}</p>
            </template>
          </el-table-column>

          <el-table-column label="操作" width="80">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
      <template>
        <el-dialog title="编辑" :visible.sync="editVisible">
          <el-form :model="entity">
            <el-form-item label="标题">
              <el-input v-model="entity.title" maxlength="50"></el-input>
            </el-form-item>
          </el-form>
          <el-form :model="entity">
            <el-form-item label="关键字 3-5个关键字（关键字与关键字之间用英文逗号隔开）">
              <el-input v-model="entity.keyword" placeholder="3-5个关键字（关键字与关键字之间用英文逗号隔开）" maxlength="80"></el-input>
            </el-form-item>
          </el-form>
          <el-form :model="entity">
            <el-form-item label="描述">
              <el-input v-model="entity.intro" maxlength="150"></el-input>
            </el-form-item>
          </el-form>

          <div slot="footer" class="dialog-footer">
            <el-button @click="editVisible=false">取 消</el-button>
            <el-button type="primary" @click="edit">确 定</el-button>
          </div>
        </el-dialog>
      </template>
    </div>
    <template>
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage1"
          :page-size="page.pageSize"
          layout="total, prev, pager, next"
          :total="page.totalSize"
        ></el-pagination>
      </div>
    </template>
  </div>
</template>

<script>
export default {
  name: "filterTable",
  data() {
    return {
      fileList: [{ name: "原图片", url: "" }],
      pageSize: 7,
      page: {},
      searchStr: "",
      addVisible: false,
      editVisible: false,
      multipleSelection: [],
      entity: {},
      headers: { token: this.$store.getters.token },
      entityList: []
    };
  },
  created() {
    this.findPage(1, this.pageSize);
  },
  methods: {
    uploadSuccess(response, file, fileList) {
      console.log(response, fileList);
      if (response.ok) {
        this.entity.picture = response.data.src;
        this.$message({
          showClose: true,
          message: "上传成功！",
          type: "success"
        });
      } else {
        this.fileList = [];
        console.log("resp失败onse", fileList);
        this.$message({
          showClose: true,
          message: "上传失败！",
          type: "error"
        });
      }

      return false;
    },
    findPage(pageNum, pageSize) {
      this.$axios({
        method: "get",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/seoSetup/findPage",
        params: { pageNum: pageNum, pageSize: pageSize },

        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          this.page = response.data;

          this.entityList = response.data.content;
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
        });
    },
    search() {
      console.log(this.searchStr);
      this.$axios({
        method: "get",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/seoSetup/findBySearchStr",
        params: { searchStr: this.searchStr },
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          this.entityList = response.data;
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
          this.$message({
            showClose: true,
            message: error,
            type: "error"
          });
        });
    },

    add() {
      this.addVisible = false;
      this.fileList = [];
      console.log("1234567", this.entity);

      this.$axios({
        method: "post",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/seoSetup/add",
        data: this.entity,
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          if (response.data.ok) {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "success"
            });
            this.findPage(1, this.pageSize);
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
          this.$message({
            showClose: true,
            message: error,
            type: "error"
          });
        });

      this.entity = {};
    },
    edit() {
      this.editVisible = false;

      this.$axios({
        method: "put",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/seoSetup/updateById",
        data: this.entity,
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          if (response.data.ok) {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "success"
            });

            this.findPage(this.page.pageNum, this.pageSize);
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
          this.$message({
            showClose: true,
            message: error,
            type: "error"
          });
        });
      this.fileList = [];
      this.entity = {};
    },
    addCategories() {
      this.entity = {};
      this.addVisible = true;
      console.log("添加");
      this.fileList = [];
    },
    handleEdit(index, row) {
      this.editVisible = true;
      console.log(index, row);
      this.entity = row;
      this.fileList[0].url = row.picture;
    },
    handleDelete(index, row) {
      this.$axios({
        method: "delete",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/seoSetup/delete",
        data: row,
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          if (response.data.ok) {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "success"
            });

            this.findPage(this.page.pageNum, this.pageSize);
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
          this.$message({
            showClose: true,
            message: error,
            type: "error"
          });
        });

      console.log(index, row);
    },
    deleteSelection() {
      console.log(this.multipleSelection);
      for (let index in this.multipleSelection) {
        this.$axios({
          method: "delete",
          // url: "http://localhost:8888/api/login",
          // method: "get",
          url: this.$baseURL + "/seoSetup/delete",
          data: this.multipleSelection[index],
          headers: {
            "Content-Type": "application/json;charset=UTF-8",
            token: this.$store.getters.token
          }
        })
          .then(response => {
            console.log(response);
            if (response.data.ok) {
              this.$message({
                showClose: true,
                message: response.data.message,
                type: "success"
              });
              if (this.page.pageNum > 1) {
                this.page.pageNum = this.page.pageNum - 1;
              }
              this.findPage(this.page.pageNum, this.pageSize);
            } else {
              this.$message({
                showClose: true,
                message: response.data.message,
                type: "error"
              });
            }
          })
          .catch(error => {
            console.log(error); //请求失败返回的数据
            this.$message({
              showClose: true,
              message: error,
              type: "error"
            });
          });
      }
    },
    filterHandler(value, row, column) {
      const property = column["property"];
      return row[property] === value;
    },
    formatter(row, column) {
      return row.address;
    },
    filterTag(value, row) {
      return row.tag === value;
    },
    handleSelectionChange(val) {
      console.log(val);
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.findPage(val, this.pageSize);
    }
  }
};
</script>

<style scoped>
.box,
.location {
  position: relative;
  height: 94%;
  overflow: scroll;
}
.location {
  /* margin-bottom: 18px; */
  height: 100%;
}
p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.search {
  width: 70%;
}
.subBtn {
  width: 10%;
  margin: 0 auto;
  float: right;
  margin-left: 10px;
}
.page {
  width: 100%;
  position: absolute;
  bottom: 0;
  background-color: #fff;
  overflow: hidden;
  text-align: center;
}
</style>



